import React, { Component } from 'react';
import { toast } from 'react-toastify';
import { ContextMenu, MenuItem, ContextMenuTrigger, SubMenu } from "react-contextmenu";
import PerfectScrollbar from 'react-perfect-scrollbar';
import SVG from 'react-inlinesvg';
import { baseUrl } from '../../../../../Assets/AssetsHelper';
import { OverlayTrigger, Tooltip } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import { GET_MEDIA_NAME } from '../../../../../Services/Utils/utils';
import { sendPublishRequest } from '../../../../../Services/Actions/publishActions'
import CollectionThumb from '../../../CommonComponents/CollectionThumb';
class AssetsCollections extends Component {
    state = {
        addCollectionShowModal: false, 
        collectionList: { assets: 0, collection: [], collectionCount: 0 }, 
        myAssetsData: [],
        collectionListRequest: true,
        assetsRequest: true,
        perPage: 10, 
        currentPage: 1,
        pageLimitOption: { value: '10', label: '10' }
    };
    addCollectionModal = event => this.setState({
        addCollectionShowModal: true
    })


    addCollectionCallback = (addCollectionEvent) => {
        this.setState({
            addCollectionShowModal: addCollectionEvent
        })
    }
    addCollectionCallbackWithAPI = (addCollectionEvent) => {
        this.onLoadCollectionList()
        this.setState({
            addCollectionShowModal: addCollectionEvent
        })
    }
    onClickToExportCollection = (collectionId, name) => event => {
        try {
            sendPublishRequest({
                "name": name,
                filterType: "AND",
                images: [],
                layout: 0,
                network: "",
                "template": 0,
                "collection": String(collectionId),
                "source": "",
                "assets_type": "",
                "tags": "",
                "ugc_status": "",
                "publish_type": 5,
                "campaign_type": "",
                "screen_type": "",
                "platform": ""
            }).then((response) => {
                toast.success("Your request submitted successfully. We will mail you Download link once your file will be ready.")
            }).catch((ex) => {
                toast.error("Please try after some time.")
            })
        }
        catch (ex) {
            toast.error("Please try after some time.")
        }
    }
    render() {
        const { collectionList, limit, history, deleteCollection } = this.props;
        return <>{collectionList.slice(0, limit).map((item, cIndex) => {
            return <div className='col-20' key={cIndex}>
                <ContextMenuTrigger id={`cm_dropDown-${cIndex}`}>
                    <Link to={`/assets/${item.id}`} className="card card-custom gutter-b text-decoration-none rounded-1">
                        <div className="card-body d-flex flex-column p-2">
                            <div className="d-flex align-items-center mr-2 w-100">
                                <CollectionThumb symbolClasses={'symbol symbol-48 flex-shrink-0 me-2 bg-light text-gray-600'} iconClass={'icon-3'} thumb={item.thumbnails} thumbName={GET_MEDIA_NAME(item.thumbnails)} />
                                <div className="overflow-hidden">
                                    <p className="fs-7 fw-medium mb-1 text-gray-800 text-ellipsis">{item.name}</p>
                                    <p className="mb-0 text-gray-600 text-ellipsis">{item.assets} Assets</p>
                                </div>
                            </div>
                        </div>
                    </Link>
                </ContextMenuTrigger>
                <ContextMenuTrigger id={`cm_dropDown-${cIndex}`}>
                    <ContextMenu className="dropdown-menu show context_menu" id={`cm_dropDown-${cIndex}`}>
                        <div className="menu-item px-2">
                            <div className="menu-content text-muted px-2 py-1 fs-8 text-uppercase">Choose your actions</div>
                        </div>
                        <div className="menu-item">
                            <MenuItem className="dropdown-item" data={{ foo: 'bar' }} onClick={event => history(`/assets/${item.id}`)}>
                                <i className="ri-folder-open-line icon-6 me-1"></i>
                                <span className="menu-title">Open</span>
                            </MenuItem>
                        </div>
                        <div className="menu-item" onClick={this.props.collectionRenameEvent(item.id, item.name)}>
                            <MenuItem className="dropdown-item" data={{ foo: 'bar' }} onClick={this.handleClick}>
                                <i className="ri-edit-box-line icon-6"></i>
                                <span className="menu-title">Rename</span>
                            </MenuItem>
                        </div>
                        <div className="menu-item">
                            <SubMenu className={`dropdown-menu px-0 show`} title={
                                <MenuItem className="dropdown-item dropdown-arrow"
                                    hoverDelay={5000}
                                >
                                    <span className="svg-icon svg-icon-5">
                                        <SVG src={baseUrl('media/icons/svg-icons/publish-line.svg')} height={16} width={16} />
                                    </span>
                                    <span className="menu-title">Publish to</span>
                                </MenuItem>
                            }>

                                <PerfectScrollbar options={{ wheelPropagation: false }}>
                                    <div className="submenu_contxt_wrap">
                                        <div className="menu-item text-decoration-none">
                                            <MenuItem className="dropdown-item" onClick={event => this.props.history(`/web-embed?collectionId=${item.id}`)}>
                                                <i className="ri-code-s-slash-line icon-6"></i>
                                                <span className="menu-title">Widget</span>
                                            </MenuItem>
                                        </div>
                                        <div className="menu-item text-decoration-none">
                                            <MenuItem className="dropdown-item" onClick={event => this.props.history(`/hashtag-campaign?collectionId=${item.id}`)}>
                                                <i className="ri-hashtag icon-6"></i>
                                                <span className="menu-title">Hashtag Campaign</span>
                                            </MenuItem>
                                        </div>
                                        <div className="menu-item text-decoration-none">
                                            <MenuItem className="dropdown-item" onClick={event => this.props.history(`/digital-screen?collectionId=${item.id}`)}>
                                                <i className="ri-tv-2-line icon-6"></i>
                                                <span className="menu-title">Digital Screen</span>
                                            </MenuItem>
                                        </div>
                                        <div className="menu-item text-decoration-none">
                                            <MenuItem className="dropdown-item" onClick={event => this.props.history(`/email-campaign?collectionId=${item.id}`)}>
                                                <i className="ri-mail-line icon-6"></i>
                                                <span className="menu-title">Email Campaign</span>
                                            </MenuItem>
                                        </div>
                                        <div className="menu-item text-decoration-none">
                                            <MenuItem className="dropdown-item" onClick={event => this.props.history(`/social-publish?collectionId=${item.id}`)}>
                                                <i className="ri-navigation-line icon-6" style={{ transform: 'scaleX(-1)' }}></i>
                                                <span className="menu-title">Social Publish</span>
                                            </MenuItem>
                                        </div>
                                        <div className="menu-item text-decoration-none">
                                            <MenuItem className="dropdown-item" onClick={event => this.props.history(`/print-media?collectionId=${item.id}`)}>
                                                <span className="svg-icon svg-icon-5">
                                                    <SVG src={baseUrl('media/icons/svg-icons/print-media-2.svg')} height={16} width={16} />
                                                </span>
                                                <span className="menu-title">Print Media</span>
                                            </MenuItem>
                                        </div>
                                        <div className="menu-item text-decoration-none">
                                            <MenuItem className="dropdown-item disabled" onClick={event => this.props.history('/social-ads')}>
                                                <OverlayTrigger placement="top" overlay={<Tooltip>Coming soon</Tooltip>}>
                                                    <>
                                                        <span className="svg-icon svg-icon-5">
                                                            <SVG src={baseUrl('media/icons/svg-icons/speaker-line.svg')} height={16} width={16} />
                                                        </span>
                                                        <span className="menu-title">Social Ads</span>
                                                    </>
                                                </OverlayTrigger>
                                            </MenuItem>
                                        </div>


                                    </div>
                                </PerfectScrollbar>
                            </SubMenu>

                        </div>

                        <hr className="my-1" />
                        <div className="menu-item" onClick={this.onClickToExportCollection(item.id, item.name)}>
                            <MenuItem className="dropdown-item">
                                <i className="ri-download-2-line icon-6"></i>
                                <span className="menu-title">Export</span>
                            </MenuItem>
                        </div>
                        <div className="menu-item" onClick={deleteCollection(item.id)}>
                            <MenuItem className="dropdown-item">
                                <i className="ri-delete-bin-6-line icon-6"></i>
                                <span className="menu-title">Move to Trash</span>
                            </MenuItem>
                        </div>
                    </ContextMenu>
                </ContextMenuTrigger>
            </div>
        })
        }</>
    }
}

export default AssetsCollections;